import * as eventType from '@/libs/eventBusType';
import baseMixin from '@/mixins';
import { mapActions } from 'vuex';
export default {
	name: 'material', //辅料审批
	mixins: [baseMixin],
	data() {
		return {
			dataList: [] //数据列表
		};
	},

















	onReady() {
		this.loadData();
	},
	onUnload() {
		// uni.$emit、 uni.$on 、 uni.$once 、uni.$off 触发的事件都是 App 全局级别的，跨任意组件，页面，nvue，vue 等
		// 使用时，注意及时销毁事件监听，比如，页面 onLoad 里边 uni.$on 注册监听，onUnload 里边 uni.$off 移除，或者一次性的事件，直接使用 uni.$once 监听
		uni.$off(eventType.BackToPage_Refresh, function(data) {
			// console.log('移除 eventType.BackToPage_Refresh 自定义事件');
		});
	},

	methods: {
		...mapActions(['searchProdPOAction']),
		//查询原纸审批 数据列表
		loadData() {
			let params = {};
			this.searchProdPOAction(params)
				.then(res => {
					//console.log(JSON.stringify(res))
					this.dataList = res.data;
				})
				.catch(err => {
					uni.showToast({
						title: '数据查询失败 err:' + err,
						icon: 'none',
						duration: 2000
					});
				});
		},
		//打开详细页面
		openDeatil(_id) {
			uni.navigateTo({
				url: './mtDetail?id=' + _id
			});
		}
	}
};
